import { MenuItem, Select } from '@mui/material';
import React, { FC, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelect from '../../hooks/useSelect';
import { sortUserAction } from '../../store/action-creators/user-action';

const Sort: FC  = () => {

    const sort = useSelect('name');

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(sortUserAction(sort.value))
    }, [sort.value])

    return (
            <Select
                defaultValue={sort.value}
                value={sort.value}
                onChange={event => {
                    sort.onChange(event.target.value)}
                }
            >
                <MenuItem value={"name"}> </MenuItem>
                <MenuItem value={"email"}> </MenuItem>
                <MenuItem value={"position"}> </MenuItem>
                <MenuItem value={"age"}> </MenuItem>
                <MenuItem value={"relation"}> </MenuItem>
            </Select>
      );
}

export default Sort; 
